@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700;900&display=swap');
html{
  height: 100%;
}
* {
  margin: 0;
  padding: 0;box-sizing: border-box;
}
body{
  font-family: 'Poppins', sans-serif;
  min-height: 100%;
  padding-bottom:60px;
  display: flex;
  flex-direction: column;
}


code {
  font-family: 'Poppins', sans-serif;
}

.mainNote{
  display: flex;
  justify-content: center;
  align-items: center;
}

form{
  display: flex;
  flex-direction: column;
}

button{
  position: relative;
  top: 26px;
  /* left: 4px; */
  z-index: 3;
  background-color: #ffffff;
}
.del{
  position: relative;
  top: 30px;
  /* left: 4px; */
  z-index: 3;
  background-color: #ffffff;
  cursor: pointer;
}
div{
  display: block;
}

footer{
  margin-top: auto;
}